<template>
  <div>
    <LayoutBox one="超市商品管理">
      <el-form ref="form" :model="form" size="mini" inline>
        <el-form-item label="商品名称">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="活动分类">
          <el-select v-model="form.activityType" placeholder="请选择">
            <!-- 活动类型 0-秒杀 1-特惠 2-普通商品 -->
            <el-option label="全部" value=""></el-option>
            <el-option label="常规" value="2"></el-option>
            <el-option label="秒杀" value="0"></el-option>
            <el-option label="特惠" value="1"></el-option>
            <el-option label="预售" value="10"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="上架状态">
          <el-select v-model="form.state" placeholder="请选择">
            <!-- 活动类型 0-秒杀 1-特惠 2-普通商品 -->
            <el-option label="全部" value=""></el-option>
            <el-option label="在售" value="1"></el-option>
            <el-option label="已售罄" value="2"></el-option>
            <el-option label="已下架" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="商品分类">
          <el-cascader v-model="form.sort" :options="sortList" @change="handleChange"></el-cascader>
        </el-form-item>
        <el-form-item>
          <el-button class="inquire1" type="success" @click="onSearch">查询</el-button>
          <el-button @click="resetForm('form')">重置</el-button>
        </el-form-item>
      </el-form>
      <div class="btn_box">
        <router-link to="/superMarket/goodsAdd"><el-button type="danger" icon="el-icon-plus" size="mini">新增商品</el-button></router-link>
        <div>
          <el-button type="primary" size="mini" @click="onSetRecommended">设为推荐</el-button>
          <el-button size="mini" @click="onCancelRecommended">取消推荐</el-button>
        </div>
        <!-- <el-button icon="el-icon-delete" size="mini">批量删除</el-button> -->
      </div>
      <el-table ref="goods" :data="goodsList" height="430">
        <el-table-column type="selection" width="55" align="center"></el-table-column>
        <el-table-column label="商品信息" align="center" width="300">
          <template slot-scope="scope">
            <div class="shopInfo_box">
              <el-image class="img" :src="scope.row.imageUrl" fit="cover"></el-image>
              <span class="text">{{ scope.row.name }}</span>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="originalPrice" label="原价" align="center"></el-table-column>
        <el-table-column prop="price" label="现价" align="center">
          <template slot-scope="scope">
            <p v-if="scope.row.price">{{ scope.row.price }}</p>
            <p v-else>{{ scope.row.originalPrice }}</p>
          </template>
        </el-table-column>
        <el-table-column label="活动分类" align="center">
          <template slot-scope="scope">
            <!-- 活动类型 0-普通 1-秒杀 2-拼团 -->
            <span v-if="scope.row.activityType === 1">秒杀</span>
            <span v-else-if="scope.row.activityType === 2">拼团</span>
            <span v-else>普通</span>
          </template>
        </el-table-column>
        <el-table-column prop="categoryName" label="品类" align="center"></el-table-column>
        <el-table-column prop="saleAmount" label="销量" align="center"></el-table-column>
        <el-table-column prop="createTime" label="创建时间" align="center" show-overflow-tooltip></el-table-column>
        <el-table-column label="状态" align="center">
          <template slot-scope="scope">
            <!-- state状态（上/下架）1-上架 0-下架 2-已售罄 -->
            <span v-if="scope.row.state === 0" style="color: #1da827">已下架</span>
            <span v-else-if="scope.row.state === 2" style="color: #e68926">已售罄</span>
            <span v-else-if="scope.row.state === 1" style="color: #de2424">在售</span>
          </template>
        </el-table-column>
        <el-table-column label="推荐" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.recommended === 1">已推荐</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center" width="130">
          <template slot-scope="scope">
            <el-button type="text" style="color: #5bd9be" @click="onUpdate(scope)">编辑</el-button>
            <el-button v-if="scope.row.state === 0" type="text" @click="onUp(scope)">上架</el-button>
            <el-button v-else-if="scope.row.state === 1" type="text" style="color: #fb8821" @click="onDown(scope)">下架</el-button>
            <!-- <el-button type="text" style="color: #5bd9be" @click="onContent(scope)">详情</el-button> -->
            <el-button type="text" style="color: #e02f2f" @click="onDelete(scope)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination background layout="prev, pager, next" :total="goodsTotal" @current-change="handleCurrentChange"></el-pagination>
    </LayoutBox>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'
import { MIXINS_QUERYPARENTNOPAGE_API, MIXINS_UPDATERECOM_API } from '@/mixins/api.js'

export default {
  name: 'Goods',

  components: {
    LayoutBox
  },

  mixins: [
    MIXINS_QUERYPARENTNOPAGE_API,
    MIXINS_UPDATERECOM_API,
  ],

  data () {
    return {
      value: '',
      goodsList: [],
      goodsTotal: 0, // 总条数
      pageNo: 1, // 分页当前页
      form: {}, // 查询表单
      sortList: [], // 分类列表
    }
  },

  created () {
    this.apiGetGoodsList()
    this.apiGetSortList()
  },

  methods: {
    // 点击分页
    handleCurrentChange (pageNo) {
      this.pageNo = pageNo
      this.apiGetGoodsList()
    },
    // 点击上架
    onUp (scope) {
      this.apiUpDown(scope, 1)
    },
    // 点击下架
    onDown (scope) {
      this.apiUpDown(scope, 0)
    },
    // 点击编辑
    onUpdate (scope) {
      var { href } = this.$router.resolve({
        path: '/superMarket/goodsAdd',
        query: { id: scope.row.id }
      })
      window.open(href)
      // this.$router.push({ path: '/superMarket/goodsAdd', query: { id: scope.row.id }})
    },
    // 点击详情
    onContent (scope) {
      var { href } = this.$router.resolve({
        path: '/superMarket/goodsFindId',
        query: { id: scope.row.id }
      })
      window.open(href)
      // this.$router.push({ path: '/superMarket/goodsFindId', query: { id: scope.row.id }})
    },
    // 点击删除
    onDelete (scope) {
      this.apiDelete(scope)
    },
    // 点击查询
    onSearch () {
      this.apiGetGoodsList()
    },
    // 点击重置
    resetForm(formName) {
      this.form = {}
    },
    // 选择分类
    handleChange (e) {
      console.log('选择分类', e[2].id)
    },
    // 设为推荐
    async onSetRecommended () {
      let id = this.$refs.goods.selection.map((item) => item.id)
      await this.apiIsRecommended(id.join(','), 1)
    },
    // 取消推荐
    async onCancelRecommended () {
      let id = this.$refs.goods.selection.map((item) => item.id)
      await this.apiIsRecommended(id.join(','), 0)
    },

    // api-------------------------------------------
    // 获取商品分页
    apiGetGoodsList () {
      let data = {
        type: 1,
        pageNo: this.pageNo
      }
      if (this.form.sort) {
        this.form.categoryId = this.form.sort[2].id
      }
      Object.assign(data, this.form)
      this.$http.post('/woho/small/product/query', data)
        .then((res) => {
          if (res.data.success) {
            this.goodsList = res.data.data.list
            this.goodsTotal = res.data.data.total
          }
        })
    },
    // 下架/上架 0-上架 1-下架
    apiUpDown (scope, state) {
      this.$http.post(`/woho/small/product/smallUpOrDown/${scope.row.id}/${state}`)
        .then((res) => {
          if (res.data.success) {
            scope.row.state = state
            switch (state) {
              case (0): {
                this.$message.success('下架成功')
                break
              }
              case (1): {
                this.$message.success('上架成功')
                break
              }
            }
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 删除
    apiDelete (scope) {
      this.$http.delete(`/woho/product/head/delete/${scope.row.id}`)
        .then((res) => {
          if (res.data.success) {
            this.goodsList.splice(scope.$index, 1)
            this.$message.success('删除成功')
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 获取分类
    apiGetSortList () {
      const loading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      this.queryParentNoPage({}).then((res) => {
        loading.close()
        if (res.data.success) {
          this.sortList = this._sortRecursive(1, '0', res.data.data, 3)
          console.log('this.sortList', this.sortList)
        }
      })
    },
    // 分类递归json重组
    _sortRecursive (level, parentId, list, maxLevel) {
      if (maxLevel < level) {
        return false
      } else {
        let letName = 'level' + level
        this[letName] = []
        this[letName] = list.filter((item) => {
          return item.level === level && item.parentId === parentId
        })
        return this[letName].map((item) => {
          return {
            value: item,
            label: item.categoryName,
            children: this._sortRecursive(level + 1, item.id, list, maxLevel),
          }
        })
      }
    },
    // 设为推荐
    async apiIsRecommended (samllId, samllType) {
      const res = await this.updateRecom(samllId, samllType)
      if (res.data.success) {
        this.$message.success('操作成功')
        this.apiGetGoodsList()
      } else {
        this.$message.error(res.data.msg)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .btn_box{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 30px;
  }
  .shopInfo_box{
    display: flex;
    justify-content: space-around;
    align-items: center;
    .img{
      width: 70px;
      height: 70px;
    }
    .text{
      width: 200px;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 3;
      overflow: hidden;
    }
  }
</style>
